@import '../../variables.scss';

.button {
    color: white;
    font-size: 18px;
    font-family: 'Josefin Sans', sans-serif;
    background: linear-gradient(90deg, $limeGreen, $brightCyan);
    padding: 15px;
    border: none;
    border-radius: 20px;
    width: 200px;
    white-space: nowrap;

    &:hover {
        opacity: 0.85;
        cursor: pointer;
    }
}

@media screen and (max-width: 770px) {
    .invisible {
        display: none;
    }
}